<template>
	<button :disabled="disabled" class="sgs-button" 
	@click="handleClick()"
	:class="[
		disabled ? 'sgs-button-disabled' : type ? 'sgs-button-' + type : '',
		!disabled && plain && type ? 'sgs-button-' + type + '-plain' : '',
		disabled && plain ? 'sgs-button-disabled-plain' : ''
	]"
	>
	<i :class="'' + icon" v-if="iconPosition=='left'||iconPosition=='top'"></i>
	<p v-if="iconPosition=='top'"></p>
	<span :class="'sgs-icon-padding-' + iconPosition"><slot>按钮</slot></span>
	<p v-if="iconPosition=='bottom'"></p>
	<i :class="'' + icon" v-if="iconPosition=='right'||iconPosition=='bottom'"></i>
	</button>
</template>

<script>
	export default {
		name: 'sgs-button',
		props: {
			type: {
				type: String,
				default: 'default'
			},
			icon: {
				type: String,
				default: ''
			},
			iconPosition: {
				type: String,
				default: ''  // left right top bottom
			},
			plain: Boolean,
			disabled: Boolean,
		},
		methods: {
			// 按钮点击事件处理
			handleClick: function() {
				this.$emit('click');
			}
		},
	}
</script>

<style scoped>

.sgs-button {
	display: inline-block;
	line-height: 1;
	white-space: nowrap;
	cursor: pointer;
	background: #fff;
	border: 1px solid #bfcbd9;
	color: #1f2d3d;
	-webkit-appearance: none;
	text-align: center;
	box-sizing: border-box;
	outline: none;
	margin: 0;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	padding: 10px 15px;
	font-size: 14px;
	border-radius: 4px;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}	

.sgs-button-default {}
.sgs-button-default:active { color: #fff;background-color: #bfcbd9;border-color: #bfcbd9; }

.sgs-button-primary { color: #fff;background-color: #20a0ff;border-color: #20a0ff; }
.sgs-button-primary:active { color: #20a0ff;background-color: #fff;border-color: #20a0ff; }

.sgs-button-info { color: #fff;background-color: #50bfff;border-color: #50bfff; }
.sgs-button-info:active { color: #50bfff;background-color: #fff;border-color: #50bfff; }

.sgs-button-success { color: #fff;background-color: #13ce66;border-color: #13ce66; }
.sgs-button-success:active { color: #13ce66;background-color: #fff;border-color: #13ce66; }

.sgs-button-warning { color: #fff;background-color: #F9C52D;border-color: #F9C52D; }
.sgs-button-warning:active { color: #F9C52D;background-color: #fff;border-color: #F9C52D; }

.sgs-button-danger { color: #fff;background-color: #ff4949;border-color: #ff4949; }
.sgs-button-danger:active { color: #ff4949;background-color: #fff;border-color: #ff4949; }


.sgs-button-primary-plain { color: #20a0ff;background-color: #fff; }
.sgs-button-primary-plain:active { color: #fff;background-color: #20a0ff; }

.sgs-button-info-plain { color: #50bfff;background-color: #fff; }
.sgs-button-info-plain:active { color: #fff;background-color: #50bfff; }

.sgs-button-success-plain { color: #13ce66;background-color: #fff; }
.sgs-button-success-plain:active { color: #fff;background-color: #13ce66; }

.sgs-button-warning-plain { color: #F9C52D;background-color: #fff; }
.sgs-button-warning-plain:active { color: #fff;background-color: #F9C52D; }

.sgs-button-danger-plain { color: #ff4949;background-color: #fff; }
.sgs-button-danger-plain:active { color: #fff;background-color: #ff4949; }

.sgs-button-disabled { color: #B3B3B3;background-color: #ccc;border-color: #ccc; }
.sgs-button-disabled-plain { color: #B3B3B3;background-color: #fff;border-color: #b3b3b3; }
.sgs-button-disabled:hover{cursor:not-allowed;}
.sgs-button-disabled-plain:hover{cursor:not-allowed;}

span { display: inline-block; text-align: center; }
.sgs-icon-padding-left { margin-left: 2px; }
.sgs-icon-padding-right { margin-right: 2px; }
.sgs-icon-padding-top { margin-top: 4px; }
.sgs-icon-padding-bottom { margin-bottom: 4px; }

@import url(../../icon-font/style.css)
</style>


